<template>
  <div class="login-wrap">

    <r-setting v-if="showType===0" @change="changeShow"></r-setting>
    <r-login v-else-if="showType===1" @change="changeShow"></r-login>
    <r-register v-else-if="showType===2" @change="changeShow"></r-register>

    <van-dialog v-model="dialogShow" title="" show-confirm-button confirm-button-text=""
      show-cancel-button cancel-button-text="" @confirm="agree" @cancel="cancel">
      <div class="dialog_message">
        <div>
          
        </div>
        <div>
          
          <span class="dialog_operate" @click="showAgreement"></span>
          
          <span class="dialog_operate" @click="showPolicy"></span>
          
        </div>
      </div>
    </van-dialog>

    <div v-show="showType!==0" class="foot_remark">
      <div class="foot_remark_content">
        <span class="dialog_operate" @click="showAgreement"></span>
        |
        <span class="dialog_operate" @click="showPolicy"></span>
      </div>
    </div>
  </div>
</template>

<script>
import RLogin from "./components/R-Login";
import RRegister from "./components/R-Register";
import RSetting from "./components/R-Setting";
import { Dialog } from "vant";
import { FuncType, sendToAPP } from "@/utils/interFlow";

export default {
  name: "login",
  components: {
    RLogin,
    RRegister,
    RSetting,
    [Dialog.Component.name]: Dialog.Component
  },
  beforeCreate() {
    this.$store.dispatch("logout");
  },
  created() {},
  data() {
    return {
      showType: 1, // 012
      dialogShow: false,
      agreementVisible: false,
      policyVisible: false
    };
  },
  mounted() {
    if (!this.$store.getters.agreeUse) {
      this.dialogShow = true;
    }
  },
  methods: {
    changeShow(val) {
      this.showType = val;
    },
    agree() {
      this.$store.commit("SET_AGREEUSE", 1);
    },
    cancel() {
      sendToAPP(FuncType.exitAPP, "");
    },
    showAgreement() {
      this.$router.push("/agreement");
    },
    showPolicy() {
      this.$router.push("/policy");
    }
  }
};
</script>

<style lang="less">
// @import url("./../../styles/common/base.less");
@font-title: 18px;
@font-sub-title: 16px;
@font-info: 14px;
.login-wrap {
  width: 100%;
  height: 100%;
  background: url("./../../assets/bg_login.png");
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  position: relative;
  .login_setting {
    position: absolute;
    right: 2vw;
    top: 2vw;
    font-size: 5vw;
  }

  .logo {
    width: 28vw;
    height: 120px;
    background: url("./../../assets/logo.png") center;
    background-size: contain;
    background-repeat: no-repeat;
    margin: 9vw 36vw;
  }
  .loginForm {
    // width: 100%;
    margin: 0 15vw;
    text-align: left;
    .formItem {
      width: 100%;
      margin-bottom: 12px;
      .iconfont {
        font-size: @font-sub-title;
        margin-right: 1vw;
        color: #999;
      }
      input {
        font-size: @font-sub-title;
        color: #999;
        padding-left: 6px;
        border: none;
        border-bottom: 1px solid rgba(153, 153, 153, 1);
        box-sizing: border-box;
      }
      .item-code {
        width: 100px;
      }
    }
  }
  .footer {
    margin-top: 2vw;
    button {
      min-width: 66vw;
      background: rgba(0, 203, 171, 1);
      border-radius: 5px;
      border: 1px solid #00cbab;
    }

    .footer_operate {
      padding: 10px 17vw;
      display: flex;
      justify-content: flex-end;
      color: #409eff;
      font-size: @font-sub-title;
    }
  }

  .dialog_message {
    padding: 12px 20px;
    text-align: left;
  }
  .dialog_operate {
    color: #409eff;
  }
}
</style>



